@import './_syntax/gruvbox-light';
@import './_gruvbox-colors';

$white: #faf6e8;

:root {
  --hima-foreground: #{$black0h};
  --hima-background: #{$white0h};

  --hima-link-color: #{$blue-dark};

  --hima-header-pinned-background: #{$white};
  --hima-header-pinned-boxshadow: #{$gray};

  --hima-nav-pinned-border: #{$white1};
  --hima-nav-menu-icon-fill: #{$black0h};
  --hima-social-icon-fill: #{$black0h};

  --hima-post-meta-color: #{$black2};

  --hima-quote-background: #{$white};
  --hima-quote-accent: #{$yellow-light};

  --hima-codeblock-accent: #{$aqua-light};
  --hima-codeblock-background: #{$white};
  --hima-inline-code-border: #{$aqua-light};
  --hima-inline-code-background: #{$white};

  --hima-table-background: #{$white};
  --hima-table-header-background: #{$white0};
  --hima-table-header-bottom-border: #{$white1};
  --hima-table-row-border: #{$white1};

  --hima-tag-foreground: #{darken($purple-dark, 8%)};
  --hima-tag-background: #{$white0s};
  --hima-tag-background-hover: #{darken($white0s, 8%)};
  --hima-tag-prefix-color: #{$black2};
  --hima-tag-suffix-color: #{$black0h};
}
